	module.exports = {"data":{"markdownRemark":{"html":"<h3>Описание:</h3>\n<p>Довольно сложное упражнение, так как необходимо соблюдать много нюансов. Упражнение не подходит для новичков - сложно научиться выполнять его правильно. Для выполнения упражнения вам понадобится силовая скамья (или что-нибудь похожее на неё).</p>\n<ul>\n  <li>чем уже хват, тем сильнее работает бицепс, но увеличивается амплитуда, что позволяет сильнее растягивать мышцы спины</li>\n  <li>широкий хват напротив – переносит нагрузку на спину, но сокращает амплитуду упражнения</li>\n</ul>\n<h3>Порядок выполнения:</h3>\n<ol>\n  <li>Исходное положение: правым коленом встаньте на скамью. Затем наклонитесь (чтобы туловище было почти параллельно полу) и упритесь в край скамьи правой рукой. Левой рукой поднимите гантель, ладонь обращена к туловищу. Спину держите прямо.</li>\n  <li>На выдохе – тяните гантель к пояснице за счет сокращения мышц спины (руки не должны участвовать).Всё туловище остается неподвижным. Старайтесь поднимать локоть повыше и не отводите руку в сторону.</li>\n  <li>На вдохе – медленно верните гантель в исходное положение.</li>\n  <li>Повторите упражнение необходимое количество раз.</li>\n  <li>Смените сторону, и повторите упражнение другой рукой.</li>\n</ol>\n<h3>Советы:</h3>\n<ul>\n  <li>В нижней точке можно разворачивать гантель перпендикулярно скамье, при этом немного опуская плечо - это лучше растянет широчайшую мышцу.</li>\n  <li>Правильно тянуть гантель так, чтобы кисть поднималась к талии. Локоть в сторону отставлять нельзя. Он должен смотреть вверх.</li>\n  <li>Это упражнение многие относят к базовым. Вес следует взять такой, чтобы Вы могли выполнить 6 - 12 повторений.</li>\n  <li>При использовании гантелей больших весов рекомендуется использовать лямки, чтобы разгрузить предплечья.</li>\n</ul>\n<h3>Видео-инструкция:</h3>\n<iframe width=\"100%\" height=\"550rem\" src=\"https://www.youtube.com/embed/PgpQ4-jHiq4\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" allowfullscreen></iframe>","frontmatter":{"path":"/exercise/tyaga-dumbbells-v-naklone","efficiency":"7","title":"Тяга гантели в наклоне","image":"/static/2-06-0.png","image_2":"/static/2-06-1.png","image_3":"","image_4":"","filterby":"back","date":"April 12, 2017","injury":"4","bodypart":"Спина","filtertype":"isolated","type":"Изолирующее","filterinventory":"dumbbells","inventory":"Гантели","filterplace":null}}},"pathContext":{}}

